<template>


  <van-nav-bar title="首页"/>

  <!--      <van-cell-group inset style="margin-top: 1rem">-->
  <van-search
      v-model="searchText"
      placeholder="请输入搜索关键词"
      input-align="center"
      action-text="hah"
      :show-action="true"
      @search="onSearchText"
  >
    <template #action>
      <div @click="onSearchText(this.searchText)">搜索</div>
    </template>
  </van-search>
  <!--      </van-cell-group>-->

  <div v-for="(e,i) in goodInfos" :key="i">
    <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }" >
      {{e.kindName}}
    </van-divider>
    <van-card
        :price="e2.GOOD_PRICE"
        :desc="e2.GOOD_DESC"
        :title="e2.GOOD_NAME"
        :thumb="e2.GOOD_IMG"
        :num="e2.GOOD_NUM"
        v-for="(e2,i2) in e.goodArray"
        :key="i2"
    >
      <template #footer>
        <van-button size="small" type="primary" :to="{name:'detail',params: {goodId:e2.GOOD_ID}}">查看详情</van-button>
        <van-button size="small" type="success">联系购买</van-button>
      </template>
    </van-card>
  </div>


</template>

<script>
import {onMounted, ref, reactive} from "vue";
import axios from "axios";
import {useRouter} from "vue-router";



export default {
  name: "HomeView",
  setup() {
    const router = useRouter();
    let searchText = ref('');

    let onSearchText = function (var1) {
      alert(var1)
    }

    let goodInfos = reactive([]);

    let init = function () {
      axios.get('/good/infos').then(function (res) {
        let resArr = res.data;
        // console.log(resArr);
        let kindArr = [];
        //分类已经排好序了
        for (let e of resArr) {
          if (kindArr[kindArr.length - 1] != e.KIND_NAME) {
            kindArr.push(e.KIND_NAME);
          }
        }
        //最终结果
        let array = [];
        for (let e2 of kindArr) {
          array.push({
            kindName: e2,
            goodArray: []
          });
        }
        let i = 0;
        for (let e3 of array) {
          for (i; i < resArr.length; i++) {
            if (e3.kindName == resArr[i].KIND_NAME) {
              e3.goodArray.push(resArr[i]);
            } else {
              for (let j = 0; j < e3.goodArray.length - 1; j++) {
                for (let k = 0; k < e3.goodArray.length - 1 - j; k++) {
                  if (e3.goodArray[k].KIND_ORDER > e3.goodArray[k + 1].KIND_ORDER) {
                    let temp = e3.goodArray[k];
                    e3.goodArray[k] = e3.goodArray[k + 1];
                    e3.goodArray[k + 1] = temp;
                  }
                }
              }
              break;
            }
          }
        }
        goodInfos.push(...array);
        console.log(goodInfos);
      })
    }

    let to=function () {
      router.push({
        name:'detail',
        state:{
          params:{id:123}
        },
        params:{id:456}
      })
    }

    onMounted(function () {
      console.log('onMounted');

      init();

    })


    return {
      searchText, onSearchText, goodInfos,to
    };
  },
}
</script>

<style scoped>

</style>
