<template>

  <div>

    <van-nav-bar title="设置"/>

    <van-cell-group inset style="margin-top: 1rem">
      <van-field v-model="Token" label="Token" placeholder="请输入Token"/>
    </van-cell-group>

    <van-cell-group inset style="margin-top: 1rem;background: #F7F8FA">
      <van-button round block type="primary" text="更改Token" @click="confirmToken"/>
    </van-cell-group>

    <van-cell-group inset style="margin-top: 1rem">
      <van-field v-model="NotionToken" label="Notion令牌" placeholder="请输入Notion令牌"
                 :disabled="!$store.state.tokenAvailable"/>
    </van-cell-group>

    <van-cell-group inset style="margin-top: 1rem">
      <van-field v-model="DataSourceID" label="Notion页面ID" placeholder="请输入Notion页面ID"
                 :disabled="!$store.state.tokenAvailable"/>
    </van-cell-group>

    <van-cell-group inset style="margin-top: 1rem;background: #F7F8FA">
      <van-button round block type="primary" text="更改设置" @click="confirmSetting"
                  :disabled="!$store.state.tokenAvailable"/>
    </van-cell-group>

    <van-cell-group inset style="margin-top: 1rem;background: #F7F8FA">
      <van-button round block type="primary" text="测试" @click="test"/>
    </van-cell-group>

  </div>


</template>

<script>
import {ref, getCurrentInstance} from "vue";
import {showNotify} from "vant";
import {useRoute} from "vue-router";
import axios from "axios";


export default {
  name: "SettingView",
  setup() {
    let vue = getCurrentInstance();
    let NotionToken = ref(localStorage.getItem('NotionToken'))
    let DataSourceID = ref(localStorage.getItem('DataSourceID'))
    let Token = ref(localStorage.getItem('Token'))

    let confirmToken = () => {
      localStorage.setItem('Token', Token.value)
      showNotify({type: 'success', message: '设置成功'});
      vue.appContext.config.globalProperties.a.auth()
    }

    let confirmSetting = () => {
      localStorage.setItem('NotionToken', NotionToken.value)
      localStorage.setItem('DataSourceID', DataSourceID.value)
      showNotify({type: 'success', message: '设置成功'});
    }

    let test=()=>{
      axios({
        method:'get',
        url:'/test',
      }).then(res => {
        console.log(res.data)
      })
    }


    return {NotionToken, DataSourceID, Token, confirmToken, confirmSetting,test}
  },
  created() {
    if (useRoute().params.showTip === 'yes') {
      console.log(useRoute().params)
      showNotify({type: 'danger', message: 'Token无效,请先设置'});
    }

  }
}
</script>

